@import "global";

.career{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: column;
  width: 58vw;
  border: 1px solid #dbdbdb;
  height: 140px;
  @include respond-to(handhelds) { width: 97vw ;}
  @include respond-to(medium-screens) { width: 97vw; }
  padding: 0;
  margin: 0 auto;

  h4{
    margin-bottom: 10px;
  }
  .term{
    color: #999;
  }
}
