@import "global";

.profile{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: column;
  width: 58vw;
  border: 1px solid #dbdbdb;
  height: 250px;
  @include respond-to(handhelds) { width: 97vw ;}
  @include respond-to(medium-screens) { width: 97vw; }
  padding: 0;
  margin: 0 auto;

  .born{
    margin-bottom: 15px;
    color: #999;
  }
  .moto{
    margin-top: 15px;
    margin-bottom: 15px;
  }
}
