@import "global";

.Modal-overlay{
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0,0,0,0.40);
}
.Modal{
    text-align: left;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    @include respond-to(handhelds) { width: 320px ;}
    @include respond-to(medium-screens) { width: 87vw; }
    width: 53vw;
    height: 85vh;
    overflow-y: scroll;
    border-radius: 10px;
    background-color: white;
    box-shadow: 0px 3px 6px rgba(0,0,0,0.16);
    & > * {
      &:first-child{
        margin-top: 16px;
      }
      margin-left: 32px;
      margin-right: 32px;
      @include respond-to(handhelds) {

      }
      @include respond-to(medium-screens) {
        margin-left: 20px;
        margin-right: 20px;
      }
    }
    p.title{
      font-size: 16pt;
    }
    .content{
      border-top: 1px solid #bebebe;
      margin-top: 16px;
      margin-bottom: 26px;
      p{
        padding: 8px;
        font-size: 12pt;
        color: #000;
      }
    }

    .button-wrap{
      margin: 0;
      margin-top: 8px;
      button{
        width: 100%;
        padding: 12px 0;
        border-radius: 0 0 10px 10px;
        background-color: #ad7cef;
        font-size: 13pt;
        color: white;
        border: 0;
        cursor: pointer;
        &:hover{
          background-color: #7f49c8;
        }
        &:active{
          background-color: #7e49c8;
        }
      }
    }

    .closeicon{
      height: 50px;
      @include respond-to(medium-screens) {
        height: 20px;
      }

      i {
        display: block;
        position: absolute;
        top: 30px;
        right: 45px;
        width: 23px;
        height: 23px;
        @include respond-to(handhelds) {
          top: 15px;
          right: 15px;
          width: 19px;
          height: 19px;
        }
        @include respond-to(medium-screens) {
          top: 20px;
          right: 30px;
          width: 23px;
          height: 23px;
        }
        cursor: pointer;
      }

      i > span{
        position: absolute;
        left: 50%;
        display: block;

        width: 28px;
        height: 2px;
        background: #555;
      }
      i > span:nth-child(1) {
          top: 50%;
          transform: rotate(45deg);
      }

      i > span:nth-child(2) {
          top: 50%;
          transform: rotate(-45deg);
      }
    }

    .img-list{
      img{
        width: 100%;
      }
    }

    .url > a{
      text-decoration: none;
      color: #82bbb5;
    }


  }

.Modal::-webkit-scrollbar{
  display: none;
}
