@import "global";

.portpolio{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 60vw;
  
  @include respond-to(handhelds) { width: 100vw ;}
  @include respond-to(medium-screens) { width: 100vw; }
  padding: 0;
  margin: 0 auto;

  li{
    width: 444px;
    height: 280px;
    @include respond-to(handhelds) {  }
    @include respond-to(medium-screens) { width: 350px; height: 200px; }
    @include respond-to(wide-screenswidth) { }
    list-style: none;
    cursor: pointer;
    background-size: 100%;
    transition: .5s all ease;
  }
  .keywordplay:hover{
    background-size: 120%;
  }
  .datalab:hover{
    background-size: 110%;
  }
  .caption{
    width: 444px;
    height: 280px;
    @include respond-to(handhelds) { }
    @include respond-to(medium-screens) { width: 350px; height: 200px; line-height: 200px; }
    @include respond-to(wide-screenswidth) { }
    text-align: center;
    color: #fff;
    line-height: 280px;
    transition: .5s all ease;
  }
  .keywordplay{
    background: url("keywordplay-main.jpg");
    background-size: 110%;
    background-position: center;
  }
  .datalab{
    background: url("datalab.png");
    background-size: 100%;
    background-position: 50% 20%;
  }
}
