@import "global";

.skill{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 60vw;
  @include respond-to(handhelds) { width: 100vw ;}
  @include respond-to(medium-screens) { width: 100vw; }
  padding: 0;
  margin: 0 auto;
}
.skill-list{
  margin-top: 10px;
  width: 170px;
  list-style: none;
  height: 100px;
  border: 1px solid #dbdbdb;

  img{
    width: 170px;
    margin-top: 5px;
  }
}
