*{ margin: 0;}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.App > h2{
  margin-bottom: 45px;
  margin-top: 45px;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  border-bottom: 1px solid #e2e8e8;
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.4);
  min-height: 6vh;
  display: flex;
  font-size: calc(8px + 1vmin);
  color: #000;
}
.gnb, .logo{
	flex: none;
  padding: 20px 30px;
}
.gnb{
  margin-left: auto;
}
.App-footer{
  border-top: 1px solid #dbdbdb;
  margin-top: 45px;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}
.App-footer > p {
  font-size: 0.9rem;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
